<script>
export default {
  name: 'CatalogCartItem',
  components: {},
  props: {
    cart_item_data: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  methods:{
    deleteFromCart(){
      this.$emit('deleteFromCart')
    }
  }
}
</script>

<template>
  <div class="catalog-cart-item">
    <div class="catalog-cart-item__wrap">
      <img :src="require('../assets/images/' + cart_item_data.image)" alt="" class="catalog-cart-item__image">
    </div>
    <div class="catalog-cart-item__info">
      <p>{{ cart_item_data.name }}</p>
      <p>{{ cart_item_data.price }}</p>
      <p>{{ cart_item_data.article }}</p>
    </div>
    <div class="catalog-cart-item__quantity">
      <p>Qty: </p>
      <p>{{cart_item_data.quantity}}</p>
    </div>
    <button @click="deleteFromCart">Delete</button>
  </div>
</template>

<style scoped>
.catalog-cart-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
  padding: 16px;
  margin-bottom: 16px;
  box-shadow: 0 0 8px 0 #e8e8e8;
}
  .catalog-cart-item__wrap{
    width: 100px;
    overflow: hidden;
  }
.catalog-cart-item__image{
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center;
  }

</style>