<template>
  <div class="catalog-item">
    <div class="catalog-item__image-wrap">
      <img :src="require('../assets/images/' + product_data.image)" alt="" class="catalog-item__image">
    </div>

    <p class="catalog-item__name">{{ product_data.name }}</p>
    <p class="catalog-item__price">Price: {{ product_data.price }} UAH</p>
    <button class="catalog-item__btn" @click="addToCart(product_data)">Add To Cart</button>
  </div>
</template>

<script>

export default {
  name: "CatalogItem",
  components: {},
  props: {
    product_data: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  methods: {
    addToCart() {
      this.$emit('addToCart', this.product_data)
    }
  }
};
</script>

<style scoped>
.catalog-item {
  flex-basis: 25%;
  box-shadow: 0 0 8px 0 #e8e8e8;
  padding: 16px;
  margin: 0 auto 16px;
  min-width: 150px;
}

.catalog-item__image-wrap {
  width: 100px;
  overflow: hidden;
  margin: 0 auto;
}

.catalog-item__image {
  object-fit: cover;
  object-position: center;
  width: 100%;
  height: 100%;
}

@media (max-width: 470px) {
  padding: 8px;
}
</style>
